export default {
  name: 'uploader',
  inject: ['$builder', '$section'],
  props: {
    imgURL: String,
    imageId: Number
  },
  data: () => ({
    src: ''
  }),
  methods: {
    updateImage() {
      const file = this.$refs.uploader.files[0];
      if (!file) {
        return;
      }
      const imageURL = URL.createObjectURL(file);
      this.src = imageURL;
      this.$section.data.images[this.imageId] = imageURL;
    }
  },
  created() {
    this.src = this.imgURL;
  }
};


